/* OVERVIEW PAGES */
.project-overview .overview {
  border-top: var(--border-fine) dashed var(--color-quiet);
  border-bottom: var(--border-fine) solid var(--color-quiet);
  padding: var(--rhythm-allegro) 0;
  width: 100%;
  font-size: var(--small);
  color: var(--color-nuance);
  padding-block: var(--rhythm-andante);
  display: flex;
  flex-direction: column;
}
.project-overview .overview .summary {
  order: 1;
}
.project-overview .overview aside {
  order: 2;
  padding-block: var(--rhythm-andante);
}
.project-overview .overview .client {
  padding-block-end: var(--rhythm-andante);
}
.project-overview .overview .client p {
  --measure:50ch;
}
.project-overview h2 {
  font-size: calc(1.25 * var(--hed-tertiary));
  padding: 0;
}
.project-overview h2, .project-overview h3 {
  color: var(--color-prominent);
}
.project-overview h2 + p, .project-overview h3 + p {
  font-size: var(--hed-quaternary);
  color: var(--color-distinct);
}
.project-overview .client, .project-overview .contribution, .project-overview .artifacts {
  max-inline-size: var(--measure);
}
.project-overview .client h2, .project-overview .contribution h2, .project-overview .artifacts h2 {
  padding-top: var(--rhythm-andante);
}
.project-overview ul {
  background: var(--background-secondary);
  border-radius: var(--radii-square);
  height: max-content;
}
.project-overview ul li {
  font-size: var(--small);
  font-family: var(--font-mono);
  list-style-type: none;
  display: flex;
  flex-direction: column;
  margin-block-end: var(--rhythm-presto);
}
.project-overview ul .label {
  text-transform: uppercase;
  font-size: var(--caption);
  letter-spacing: 1.25%;
  font-feature-settings: "smcp";
  font-weight: var(--weight-bold);
}
.project-overview .overview, .project-overview .prose {
  max-inline-size: none;
  max-width: none;
}
.project-overview .contribution {
  padding-block-end: var(--rhythm-andante);
  --measure: 84ch;
  width: 100%;
  border-bottom: var(--border-fine) solid var(--border-color-nuance);
}
.project-overview .contribution p {
  --measure:50ch;
}

.project-overview .cta {
  background: var(--background-tertiary);
  padding: var(--rhythm-andante);
  margin-block: var(--rhythm-andante);
  border-radius: var(--radii-square);
  display: flex;
  flex-flow: column;
  max-inline-size: var(--measure);
  width: 100%;
  align-items: center;
  row-gap: var(--rhythm-moderato);
}
.project-overview .cta h3 {
  padding: 0;
}

.button:is(.case-details) {
  --button-gap: var(--rhythm-presto);
  border-color: var(--border-color-distinct);
  background: transparent;
  width: max-content;
  padding-inline: var(--rhythm-moderato);
  color: var(--color-distinct);
  margin-block-start: 0;
}
.button:is(.case-details):hover {
  border-color: var(--border-color-emphasis);
  color: var(--color-emphasis);
  background: var(--background-tertiary);
}

.button:is(.case-details)::before {
  content: "";
  position: relative;
  align-self: center;
  height: var(--prose);
  width: var(--prose);
  display: inline-block;
  background: var(--color-distinct);
  mask-image: url("/assets/svg/icons/read-me.svg");
  mask-mode: alpha;
  mask-repeat: no-repeat;
  mask-size: var(--prose);
  mask-position: center;
  margin-inline-end: var(--rhythm-presto);
  margin-inline-start: var(--rhythm-presto);
}

.case-details:hover::before, .case-details:active::before {
  background: var(--color-prominent);
}

@media screen and (min-width: 40em) {
  .project-overview .overview {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row;
    grid-template-areas: "summary summary aside";
    column-gap: var(--rhythm-andante);
  }
  .project-overview .overview .summary {
    grid-area: summary;
  }
  .project-overview .overview aside {
    grid-area: aside;
    padding-block-start: 0;
  }
  section:has(.objectives, .results) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: var(--rhythm-moderato);
    max-inline-size: none;
    width: 100%;
  }
  .project-overview .cta {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    align-items: baseline;
  }
}
